@import url('https://fonts.googleapis.com/css2?family=Sora:wght@100;200;300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700;800;900&family=Poiret+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');

*{
  outline: none;
  /* -webkit-tap-highlight-color: transparent;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none; */
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
svg {
  height: 1rem;
}
html{
  height: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
body::-webkit-scrollbar {
  width: 0px;
}
body {
  --cbl: #1FB4FA;
  --cbd: #2F85AD;

  --crl: #FA5066;
  --crd: #AD2638;

  --cyl: #FAFFE8;
  --cyd: #FAF469;

  --cgl: #b3ed50;
  --cgd: #84c219;

  --col: #f58b1b;
  --con: #E47500;
  --cod: #DC7000;

  --fsor: 'Sora', sans-serif;
  --fpoi: 'Poiret One', sans-serif;
  --fcin: 'Cinzel', serif;
  --fpop: 'Poppins', sans-serif;

  --xxsf: clamp(0.6rem, 2vw, 0.8rem);
  --xsf: clamp(0.8rem, 2vw, 1.2rem);
  --sf: clamp(1rem, 3vw, 1.5rem);
  --mf: clamp(1.3rem, 4vw, 2rem);
  --bf: clamp(1.5rem, 6vw, 2.5rem);
  --xbf: clamp(2rem, 6vw, 3.5rem);

  font-family: var(--fpop);
  position: relative;
  overflow-x: hidden;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
